<template>
    <div class="about">
        <input class="sea_input" type="text" placeholder="SEARCH" v-model="search">
        <div class="a_content">
            <template v-for="(color ,indexs) in hex_list">
                <div @click="what_color(indexs)" class="hex_box" :key="indexs" v-if="color.tag.join().includes(search.toLowerCase())">
                    <div class="hex_card" >
                        <img :src="require(`./img/${color.img}`)">
                        <p>{{ color.hex }}</p>
                    </div>
                </div>
            </template>
        </div>
        <!-- <transition name="all" v-show="bounce"> -->
            <div class="alert" v-show="alert_TF">
                <span class="close" @click="close">
                    <span></span><span></span>
                </span>
                <div class="a_cimg"><img :src="require(`./img/${hex_alert_name}.png`)"/></div>
                <div class="a_ctxt" >
                    <p>{{ hex_alert_name }}</p>
                    <div class="rgb"><div class="r rgb_c">{{ hex_alert_r }} ({{r_per}}%)</div></div>
                    <div class="rgb"><div class="g rgb_c">{{ hex_alert_g }} ({{g_per}}%)</div></div>
                    <div class="rgb"><div class="b rgb_c">{{ hex_alert_b }} ({{b_per}}%)</div></div> 
                    <input type="button" value="HEX COPY" @click="copy">
                    <input type="button" value="Check the similar hex." @click="check_hex">
                </div>
            </div>
        <!-- </transition> -->
    </div>
</template>

<script>
import hex1 from './js/list'
import $ from 'jquery'; // eslint-disable-line no-unused-vars
/* eslint-disable spaced-comment */
    export default {
        data() {
            return {
                playlist : [
                    {title : 'Vue.js'},
                    {title : 'React.js'},
                    {title : 'Angular.js'}
                ],
                search : '',
                hex_list : hex1,
                hex_alert_name : "none",
                hex_alert_tag : '',
                hex_alert_r : 0,
                hex_alert_g : 0,
                hex_alert_b : 0,
                r_per : "0",
                g_per : "0",
                b_per : "0",
                alert_TF : true,
                rgb_g : [0,0,0],
                resize_w : 0,
                resize_h : 0,
                scroll_T : 0,
            }
        },
        methods : {
            what_color(indexs) {
                this.alert_TF = true;
                this.hex_alert_name = this.hex_list[indexs].hex; //해당객체 불러오기
                this.hex_alert_img = this.hex_list[indexs].img; //해당객체 불러오기
                this.hex_alert_tag = this.hex_list[indexs].tag;
                this.hex_alert_r = this.hex_list[indexs].red;
                this.hex_alert_g = this.hex_list[indexs].green;
                this.hex_alert_b = this.hex_list[indexs].blue;
                this.r_per = (this.hex_alert_r *100 / (this.hex_alert_r + this.hex_alert_g + this.hex_alert_b)).toFixed(2)
                this.g_per = (this.hex_alert_g *100 / (this.hex_alert_r + this.hex_alert_g + this.hex_alert_b)).toFixed(2)
                this.b_per = (this.hex_alert_b *100 / (this.hex_alert_r + this.hex_alert_g + this.hex_alert_b)).toFixed(2)
                $('.r').stop().animate({'width' : this.r_per + '%'})
                $('.g').stop().animate({'width' : this.g_per + '%'})
                $('.b').stop().animate({'width' : this.b_per + '%'})
                this.rgb_g = [this.hex_alert_r, this.hex_alert_g, this.hex_alert_b];
            },

            close() {
                this.alert_TF = false;
            },

            copy() { 
                const aux = document.createElement("input"); //aux는 input
                aux.setAttribute("value",this.hex_alert_name); //aux(input)의 value는 this.hex_alert_name
                document.body.appendChild(aux); //aux는 body의 자식
                aux.select(); //모든 요소(텍스트)를 선택함(드래그)
                document.execCommand('copy'); //클립보드에 현재 선택 영역의 내용을 복사합니다
                document.body.removeChild(aux); //body중 aux를 반환
            },

            check_hex() { /* 관련 색상 검색 기능 */
                // var max = this.rgb_g[0];
                // var max_index = 0;
                // for(var i=0; i<this.rgb_g.length; i++) {
                //     if(this.rgb_g[i] > max) {
                //         max = this.rgb_g[i]
                //         max_index = i;
                //     }
                // }

                // console.log(max_index);
                // if(max_index == 0) {
                //     this.search = "red"
                // }else if(max_index == 1) {
                //     this.search = "green"
                // }else if(max_index == 2) {
                //     this.search = "blue"
                // } 
                // 문제점 : #FFF 와 #000등 실제로 보이는 색과 검색되는 색상이 다를 확률이 높음
                // 해결 : tag[1]을 대표색상으로 지정하고 검색하게함
                this.search = this.hex_alert_tag[1];
                if(this.resize_w < 1024) {
                    this.alert_TF = false;
                }
            },

            resize_Event() { /* resize전용 이벤트 resize가 될 때 마다 실행된다*/
                this.resize_w = window.innerWidth;
                this.resize_h = window.innerHeight;
                if(this.resize_w < 1024) {
                    this.alert_TF = false;
                }else if(this.resize_w > 1024) {
                    this.alert_TF = true;
                }
            },

            scroll_event() {
                this.scroll_T = $(window).scrollTop();
                console.log(this.scroll_T);
                var he = document.querySelector('.header1')
                var h_scroll = he.classList.contains('he_add')
                if(this.scroll_T > 100 ) {
                    he.classList.add('he_add')
                }else if(this.scroll_T == 0 && h_scroll){
                    he.classList.remove('he_add')
                }
            }
        },
        filters: {
        },
        mounted()  { /* 새로고침 인식 */
            setTimeout(() => {
            var r2 = this.$route;
            // console.log("tf22 : " + r2.params.aaaa);
            if(r2.params.aaaa != undefined) { /* 값이 존재할 때만 작동 */
                this.search = r2.params.aaaa.toLowerCase();
            }
            },100)
            // reisze
            window.addEventListener('resize', this.resize_Event);
            this.resize_w = window.innerWidth;
            if(this.resize_w < 1024) {
                this.alert_TF = false;
            }else if(this.resize_w > 1024) {
                this.alert_TF = true;
            }

            // scroll
            window.addEventListener('scroll', this.scroll_event);
            // this.scroll_T = $(window).scrollTop();
        },
        beforeDestory() { /* 컴포넌트가 제거 될 때 실행되는 라이프 사이클 */
        // resize
        window.removeEventListener('resize', this.resize_Event);
        // scroll
        window.removeEventListener('scroll', this.scroll_event);
        },

    }


</script>
<style>
@import "./css/about.css";
@import "./css/css_m.css";
</style>