<template>
    <div class="home">
        <div class="home_main">
            <div class="h_ibox">
                <img :src="require(`./img/home_1.jpg`)">
                <h1>“조금은 미쳐도 좋아, 새로운 색깔로 세상을 보기 위해서라면”</h1>
                <p>영화 라라랜드 (La La Land), 2016, 미아의 대사</p>
            </div>
        </div>
        <h1>오늘은 이런 색 어때요 ? </h1> <p>{{c_text}}</p>
        <div class="h_content">
            <template v-for="color in hex_list" >
                <div :key="color" @click="asd(color.hex)" class="hex_box" v-if="color.tag.join().includes(day)">
                    <router-link 
                    router :to="{path : '/about', name : 'about', id:'11', aaaa:color.hex}">
                    </router-link>
                    <div class="hex_card">
                        <img :src="require(`./img/${color.img}`)">
                        <p>{{color.hex}}</p>
                    </div>
                </div>
            </template>
        </div>
    </div>
</template>

<script>
import hex1 from './js/list'

export default {
    data() {
        return {
            hex_list : hex1,
            day : 'red',
            c_text : 'asd',
        }
    },

    methods: {
        asd(color) {
            this.$router.push({
                name: "about",
                params: { name : 'about', aaaa: color}
            });
        }
    },

    mounted()  {
        setTimeout(() => {
            const random = Math.floor(Math.random() * 6 + 1);
            if(random == 1) {
                this.day = 'red'
                this.c_text = "“빨간색은 스펙트럼의 양쪽에 있기 때문에 감정과 연관되는 흥미로운 색상이다.”"
            }else if(random == 2) {
                this.day = 'yellow'
                this.c_text = "“태양과 햇빛을, 나는 달리 표현 할 수가 없어 노란색, 옅은 유황색, 연한 황금빛 레몬색이라 부른다. 이 얼마나 아름다운 노랑인가!”"

            }else if(random == 3) {
                this.day = 'green'
                this.c_text = "“친애하는 친구여, 모든 이론은 회색 빛 이지만, 인생의 황금 나무는 초록이라네”"
            }else if(random == 4) {
                this.day = 'blue'
                this.c_text = "“다른 모든 색을 뛰어넘는 빛나고 아름답고 완벽한 색. 어느 누구도 이 색에 대해 말할 수 없고, 이것으로 아무것도 할 수 없다. 이 색의 질은 여전히 독보적으로 뛰어나다.”"
            }else if(random == 5) {
                this.day = 'purple'
                this.c_text = "“보라색은 마지막까지 상대방을 믿고 서로서로 사랑하자는 뜻이에요.”"
            }else if(random == 6) {
                this.day = 'black'
                this.c_text = "“블랙 없이는 샤넬도 없다. 존재 자체가 불가능하다.”"
            }
        })

    },

    watch: { //어떤 data가 변경이 이루어 질 때 이 안에있는 함수들이 실행됨 

    }
}
</script>