<template >
    <div class="header1">
        <h1>PASTEL</h1>
        <div class="menu_tab">
            <input v-for="(bt, ind) in b_bt" :key="ind" class="menu_input" type="button" :value="bt.name" @click="inc(ind)" >
        </div>
    </div>
</template>

<script>
import $ from 'jquery'; // eslint-disable-line no-unused-vars

export default {
    data()  {
        return {
            b_bt : [
                {name : 'HOME'},
                {name : 'COLOR_LIST'}
            ],
            count : 0,
        }
    },
    // name : "header1"
    methods: {
        inc(ind) {
            var router = this.$router;
            if(ind == 0 ) { //home
                router.push('/')
            }else if(ind == 1) { //about
                router.push("/about")
            }
            var menu_input = document.querySelectorAll('.menu_input');
            menu_input[this.count].classList.remove("h_put");
            this.count = ind
            menu_input[this.count].classList.add("h_put");
        },

        scsc() {
            window.addEventListener('scroll', function(){
                console.log('asdasd');
            });
        }
 

    },
    
    mounted() {
        setTimeout(() => {
            var r2 = this.$route;

            if(r2.name == 'Home') { //name = Home 일 경우
            }else if(r2.name == "about") { //name = about 일 경우
                this.count = 1;
            } 

            var menu_input = document.querySelectorAll('.menu_input');
            menu_input[this.count].classList.add("h_put");
        });
    },
    watch: {
    '$route' (to, from) {// eslint-disable-line no-unused-vars
            setTimeout(() => {
            var r2 = this.$route;
            var menu_input = document.querySelectorAll('.menu_input');
            menu_input[this.count].classList.remove("h_put");
            if(r2.name == 'Home') { //name = Home 일 경우
                this.count = 0;
            }else if(r2.name == "about") { //name = about 일 경우
                this.count = 1;
            } 
            menu_input[this.count].classList.add("h_put");
        });
    }
  }


};






</script>

